@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Kalam:wght@300;400;700&family=Orbitron:wght@600&display=swap');

body{
    background-color: yellowgreen;
}
.main-div{
    display: flex;
    gap: 80px;
    width: 1250px;
    height: 600px;
    border: 2px solid black;
    margin: 50px 100px;
    padding: 8px 40px ;
    justify-content: center;
    border-radius: 30px;
    background-color: lightpink;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.4);
}




.line{
    margin-top: 50px;
    height: 500px;
    border: 2px solid black;
}

.todo-div input{
    height: 30px;
    width: 230px;
    border-radius: 10px;
    box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.4);
}

.head{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border: 2px solid black;
    text-align: center;
    height: 50px;
    box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.4);
    background-color: khaki;
    border-radius: 5px;
}

.todo-div ol li{
    list-style: none;
    margin-top: 10px;
}

.prog-div ol li{
    list-style: none;
}

.comp-div ol li{
    list-style: none;
}

.todo-div input:hover{
   border-color: aqua;
}

.prog-div input{
    height: 30px;
    width: 230px;
    border-radius: 10px;
    box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.4);
}

.prog-div input:hover{
   border-color: aqua;
}

.comp-div input{
    height: 30px;
    width: 230px;
    border-radius: 10px;
    box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.4);
}

.comp-div input:hover{
   border-color: aqua;
}

.btn{
    display: flex;
    gap: 10px;
}

.btn1{
    border-radius: 50px;
    width: 35px;
    font-size: 25px;
    cursor: pointer;
}

.btn1:hover{
    background-color: green;
    color: white;
}

.btn2{
  width: 70px;
  height: 30px;
  margin-top: 10px;
  cursor: pointer;
  border-radius: 10px;
}

.btn2:hover{
    background-color: red;
    color: white;
}

.flx1{
    display: flex;
    gap: 35px;
}

.flx1 p {
   font-size: 17px;
}

.list{
    display: flex;
   
    font-size: 20px;
}


.break{
    width: 180px;
    font-family: cursive;
    font-weight: 400;
}

.icons{
    font-size: 15px;
    gap: 10px;
    display: flex;
    /* align-items: center; */
    margin-left: -5px;
    cursor: pointer;
}

.fa-trash:hover{
   color: white;
}

.para{
    font-family: cursive;
    
}



.time{
    height: 30px;
    background-color: powderblue;
    border: none;
    border-radius: 10px;
}

.time:hover{
    cursor: pointer;
    background-color: cadetblue;
}

.scroll{
    overflow:visible;
    overflow-x: hidden;
    word-wrap: break-word;
    height: 325px;
    padding: 0px;
    width: 280px;
    margin-left: 20px;
  
}


.check:hover{
    color: green;
    cursor: pointer;
}

.arr{
    margin-top: 0px;
}

@media screen and (max-width: 426px) {
    .main-div{
       background-color: transparent ;
        display:flex;
        flex-wrap: wrap;
        flex-direction:column;
        border: none;
        padding: 0px ;
        gap: 30px;
        margin: 20px 20px ;
        /* margin-left: 170px; 768*/
        width: 300px;
        height: 2300px;
        box-shadow: none;
        /* justify-content: center; */
    }

    .line{
        width: 370px;
        border: 2px solid black ;
        height: 0px;
        margin-top: 20px;
    }

    .todo-div{
        background-color: lightpink;
        width: 375px;
        height: 700px;
        border-radius: 20px;
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.4);
        margin-top: 0px;
       
    }

    .prog-div{
        background-color: lightpink;
        width: 375px;
        height: 700px;
        border-radius: 20px;
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.4);
    }

    .comp-div{
        background-color: lightpink;
        width: 375px;
        height: 700px;
        border-radius: 20px;
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.4);
    }

    .head{
        border: 2px solid black;
        text-align: center;
        height: 50px;
        width: 250px;
        box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.4);
        background-color: khaki;
        border-radius: 5px;
        margin-left: 60px;
    }

    .inputmain{
        margin-left: 50px;
    }

    .scroll{
        overflow:visible;
        overflow-x: hidden;
        word-wrap: break-word;
        height: 400px;
        padding: 0px;
        width: 330px;
        margin-left: 20px;
      
    }

    .list{
        margin-left: 30px;
    }

    .icons{
        margin-left: 27px;
       
    }
    
}



